const mastersReducer = (state = initialState, action) => {
	switch (action.type) {
		case FOLLOW:
		case UNFOLLOW:
		default:
			return state;
	}
}

export const followActionCreator = (userId) => ({type: FOLLOW, userId});
export const unFollowActionCreator = (userId) => ({type: UNFOLLOW, userId});


export default mastersReducer;